}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.no-scroll {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
